import React from 'react';
import { Card } from 'react-bootstrap';
import Intro from './Intro';
import Ec from './Ec';
import data from '../data/teacher/teacher_class_data.json';
import link from '../data/teacher/teacher_class_link.json';

export default class extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            bodyH: 0,
        };
        this.headerDom = React.createRef();
        this.content = [
            '1. 鼠标放在图块上滚动即可高亮该图块信息',
            '2. 教师图块高度表示该教师带班数量',
            '3. 班级图块高度表示该班级教师数量',
            '设计理念: 对比教师带班数量，帮助校领导层了解教师压力，平衡教师带班数量',
        ];
        this.option = {
            color: ['#90ca8f', '#72a375', '#71b9bc', '#7189aa', '#efdd7d', '#e79d88', '#df6b66'],
            series: [
                {
                    type: 'sankey',
                    left: '16%',
                    right: 0,
                    top: 5,
                    bottom: 5,
                    focusNodeAdjacency: 'allEdges',
                    itemStyle: {
                        borderWidth: 0,
                    },
                    draggable: false,
                    data: data,
                    links: link,
                    label: {
                        position: 'left'
                    },
                    lineStyle: {
                        normal: {
                            color: 'source',
                            curveness: 0.5
                        }
                    }
                }
            ]
        };
    }
    componentWillReceiveProps(nextProps) {
        this.setState({
            bodyH: nextProps.height - this.headerDom.current.clientHeight - 2
        });
    }
    render() {
        return (
            <Card>
                <div className='card-header' ref={this.headerDom}>
                    <h4>1-2-3 教师-班级关系</h4>
                    <Intro content={this.content} />
                </div>
                <Card.Body className='overflow-y-scroll' style={{height: this.state.bodyH + 'px'}}>
                    <Ec option={this.option} h='6000px'/>
                </Card.Body>
            </Card>
        );
    }
}