import React from 'react';
import Teacher1 from '../../components/SchoolTeacher1';
import Teacher2 from '../../components/SchoolTeacher2';
import Teacher3 from '../../components/SchoolTeacher3';

export default class extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            t3H: 0,
        };
        this.t3Dom = React.createRef();
    }
    componentDidMount() {
        this.setState({
            t3H: this.t3Dom.current.clientHeight,
        });
    }
    render() {
        return (
            <div className='row mb-3'>
                <div className='col-12 col-lg-8'>
                    <div className='row mb-3'>
                        <div className='col-lg'>
                            <Teacher1 />
                        </div>
                    </div>
                    <div className='row'>
                        <div className='col-lg'>
                            <Teacher2 />
                        </div>
                    </div>
                </div>
                <div className='col-12 col-lg-4' ref={this.t3Dom}>
                    <Teacher3 height={this.state.t3H}/>
                </div>
            </div>
        );
    }
}
