import React from 'react';
import { Card } from 'react-bootstrap';
import Ec from './Ec'
import Intro from './Intro';
import StudentScore from '../data/student/Student_Score_1.json'

// 展示学生的各个学科的成绩和排名，使用不同的legend表示，其中包含成绩包含班级的平均分，排名包含预测
const xlabel = StudentScore.xlabel;
const sub_rank = StudentScore.sub_rank;
const sub_rank_predict = StudentScore.sub_rank_predict;
const sub_rank_down = StudentScore.sub_rank_down;
const sub_rank_up = StudentScore.sub_rank_up;

export default function(){
    const option = {
        legend: {
            data: ['历史排名', '预测排名', '置信区间'],
            right: 'right'
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                axis: 'x',
                type: 'line'
            }
        },
        grid:{
            left: 0,
            right: 0,
            top: '20%',
            bottom: '5%',
            containLabel: true
        },
        xAxis: {
            type: 'category',
            axisTick: {
                show: false
            },
            axisLabel: {
                show: false
            },
            data: xlabel
        },
        yAxis: {
            name: '排名',
            nameLocation: 'start',
            min: 1,
            max: 50,
            inverse: true,
            type: 'value',
            axisTick: {
                show: false
            },
            splitLine: {
                lineStyle: {
                    color: '#eeeeee',
                    type: 'dashed'
                }
            }
        },
        series: [{
            name: '预测排名',
            data: sub_rank_predict,
            itemStyle: {
                color: '#df6b66'
            },
            type: 'line'
        },{
            name: '历史排名',
            data: sub_rank,
            itemStyle: {
                color: '#749aa0'
            },
            type: 'line'
        },{
            name: '置信区间',
            data: sub_rank_down,
            type: 'line',
            itemStyle: {
                color: '#bdc0ba',
            },
            lineStyle: {
                    normal: {
                        opacity: 0
                    }
                },
                stack: 'confidence-band',
        },{
            name: '置信区间',
            data: sub_rank_up,
            type: 'line',
            itemStyle: {
                color: '#bdc0ba',
            },
            lineStyle: {
                    normal: {
                        opacity: 0
                    }
                },
                areaStyle: {
                    normal: {
                        color: '#ccc'
                    }
                },
                stack: 'confidence-band',
        }]
    };
    return (
        <Card>
            <Card.Header>
                <h4>3-4 语文排名趋势</h4>
                <Intro content={[
                    '图中折线代表学生历次考试排名趋势，根据教育学研究结果，图中阴影部分代表学生下次考试的置信区间，置信区间范围取决于当前成绩与历次成绩评估结果。如处于区间内则属于正常波动，如超出区间范围，则需要教师家长介入辅导'
                ]} />
            </Card.Header>
            <Card.Body>
                <Ec option={option} h='180px' />
            </Card.Body>
        </Card>
    )
}