import React from 'react';
import { Card } from 'react-bootstrap';
import Ec from './Ec';
import Intro from './Intro';
import jsonData from '../data/consumption/school/poor_stu_csp_money.json';

const getSeries = () => {
    const stuIndex = [0, 51, 128, 299, 306, 457, 552, 675, 821, 988, 1164, 1296, 1462, 1621, 1777, 1922, 2088, 2260, 2429, 2543, 2712, 2719, 2746, 2863, 3022, 3173, 3265, 3333, 3428, 3540, 3661, 3683, 3710, 3862, 3884, 3925, 4087, 4157, 4277, 4414, 4536, 4698, 4864, 5033, 5047, 5168, 5283, 5427, 5544, 5634, 5709, 5806, 5936, 5998, 6125, 6186, 6233]
    let series = [];
    for (let i = 0; i < stuIndex.length - 1; i ++) {
        let arr = [];
        let pre = '';
        let cnt = 0;
        for (let j = stuIndex[i]; j < stuIndex[i+1]; j ++) {
            const singleData = jsonData.data[j];
            if (singleData[0] !== pre) {
                cnt = 0;
                if (arr[0]) {
                    arr[0].push([singleData[0], singleData[1]]);
                } else {
                    arr[0] = [];
                    arr[0].push([singleData[0], singleData[1]]);
                }
                pre = singleData[0];
            } else {
                if (arr[cnt]) {
                    arr[cnt].push([singleData[0], singleData[1]]);
                } else {
                    arr[cnt] = [];
                    arr[cnt].push([singleData[0], singleData[1]]);
                }
            }
            cnt += 1
        }
        // 此时arr中存放的是1个学生的数据
        for (let k = 0; k < arr.length; k ++) {
            series.push({
                name: jsonData.index[stuIndex[i]],
                type: 'bar',
                data: arr[k],
            });
        }
    }
    return series;
}

export default function() {
    const option = {
        color: ['#749aa0', '#df6b66', '#e79d88', '#8cc1aa', '#ec7e55', '#72a375', '#efdd7d', '#71B9BC', '#7189aa', '#90CA8F', '#F59F48'],
        grid: {
            left: 0,
            right: '2%',
            top: '12%',
            bottom: 0,
            containLabel: true
        },
        legend: {
            type: 'scroll',
            selectedMode: 'single',
        },
        tooltip: {
            trigger: 'axis',
            axisPointer: {
                axis: 'x',
                type: 'shadow'
            }
        },
        xAxis: {
            type: 'time',
            axisTick: {
                show: false
            },
            splitLine: {
                show: false
            },
            axisLabel: {
                formatter: (value, index) => {
                    const date = new Date(value);
                    const texts = [(date.getMonth() + 1), date.getDate()];
                    return texts.join('/');
                }
            },
        },
        yAxis: {
            type: 'value',
            axisLabel: {
                formatter: '{value}元'
            },
            axisTick: {
                show: false
            },
            splitLine: {
                lineStyle: {
                    color: '#eeeeee',
                    type: 'dashed'
                }
            }
        },
        series: getSeries()
    };
    const content = [
        '设计理念: 对于消费次数偏多的贫困生，可以单独查看该贫困生的历史消费金额，进一步帮助校领导层了解贫困生的消费情况',
    ];
    return (
        <Card>
            <Card.Header>
                <h4>1-5-5 贫困生消费金额总览</h4>
                <Intro content={content} />
            </Card.Header>
            <Card.Body>
                <Ec option={option} h='400px'/>
            </Card.Body>
        </Card>
    );
}