import React from 'react';
import { Card } from 'react-bootstrap';
import Ec from './Ec';
import jsonDateGender from '../data/consumption/school/date_gender.json';

export default function() {
    let maleMoney = [];
    let femaleMoney = [];
    let meanMoney = [];
    let maleNum = [];
    let femaleNum = [];
    for (let i = 0; i < jsonDateGender.data.length; i ++) {
        const singleData = jsonDateGender.data[i];
        if (singleData[1] + singleData[2] >= 1000) {
            const date = singleData[0].replace(/\//g, '-');
            maleMoney.push([date, singleData[4].toFixed(2)]);
            femaleMoney.push([date, singleData[5].toFixed(2)]);
            meanMoney.push([date, ((singleData[4] + singleData[5]) / (singleData[1] + singleData[2])).toFixed(2)]);
            maleNum.push([date, singleData[1]]);
            femaleNum.push([date, singleData[2]]);
        }
    }
    const option = {
        color: ['#df6b66', '#749aa0', '#8cc1aa'],
        tooltip : {
            trigger: 'axis',
            axisPointer: {
                axis: 'x',
                type: 'line',
            }
        },
        legend: {
            top: 'top',
            left: 'center',
        },
        grid: {
            left: 0,
            right: '1%',
            top: '12%',
            bottom: 0,
            containLabel: true
        },
        xAxis : [
            {
                type : 'time',
                boundaryGap : false,
                axisTick: {
                    show: false
                },
                splitLine: {
                    show: false
                },
                axisLabel: {
                    formatter: (value, index) => {
                        const date = new Date(value);
                        const texts = [(date.getMonth() + 1), date.getDate()];
                        return texts.join('/');
                    }
                },
            }
        ],
        yAxis : [
            {
                name: '消费金额',
                type : 'value',
                axisLabel: {
                    formatter: '{value}元'
                },
                axisTick: {
                    show: false
                },
                splitLine: {
                    show: false,
                    lineStyle: {
                        color: '#eeeeee',
                        type: 'dashed'
                    }
                }
            },
            {
                name: '消费人数',
                type: 'value',
                axisLabel: {
                    formatter: '{value}人'
                },
                axisLine: {
                    show: false
                },
                axisTick: {
                    show: false
                },
                splitLine: {
                    show: false,
                    lineStyle: {
                        color: '#eeeeee',
                        type: 'dashed'
                    }
                }
            },
            {
                show: false,
                type: 'value',
                max: 300,
            }
        ],
        series : [

            {
                name: '女生金额',
                type: 'line',
                stack: '金额',
                symbol: 'none',
                smooth: true,
                areaStyle: {
                    opacity: 1,
                },
                data: femaleMoney
            },
            {
                name: '男生金额',
                type: 'line',
                stack: '金额',
                symbol: 'none',
                smooth: true,
                areaStyle: {
                    opacity: 1,
                },
                data: maleMoney
            },
            {
                name: '人均金额',
                type: 'line',
                yAxisIndex: 2,
                symbol: 'none',
                smooth: true,
                data: meanMoney
            },
            {
                name: '女生人数',
                type: 'line',
                yAxisIndex: 1,
                symbol: 'none',
                smooth: true,
                data: femaleNum
            },
            {
                name: '男生人数',
                type: 'line',
                yAxisIndex: 1,
                symbol: 'none',
                smooth: true,
                data: maleNum
            },
        ]
    };
    return (
        <Card>
            <Card.Header>
                <h4>1-5-2 消费趋势</h4>
            </Card.Header>
            <Card.Body>
                <Ec option={option} h='280px'/>
            </Card.Body>
        </Card>
    );
}